@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;600&display=swap);
.background_shade {
    position: absolute;
    top: 0;
    left: 0;
    max-height: 65px;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.829);
}

.title {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    color: rgba(136, 226, 233, 0.774);
    font-size: 1.5rem;
    text-transform: uppercase;
    font-weight: 700;
}
.card{
    height: 150px;
    width: 250px;
    border-radius: 10px;
    background: #cde2ecb4;
    /* Position */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* Box Shadow */
    box-shadow: -2px -2px 10px rgba(255,255, 255, 0.3),
                -5px -5px 10px rgba(0,0, 0, 0.1) inset;
    margin: 25px;
}

.name{
    font-size: 2rem;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 2px;
    color: rgba(0,0, 0, 0.829);
    margin: 10px 0px;
}
.value{
    font-size: 1rem;
    font-weight: 600;
    letter-spacing: 2px;
    color: rgba(0,0, 0, 0.5);
    margin: 10px 0px;
}
.content{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.content-box{
    display: flex;
    flex-direction: column;  
    width: 30%;
    height: 80%;
    box-shadow: 2px 9px 14px -1px #000;
    justify-content: center;
    align-items: center; 
    overflow-x: hidden;
    overflow-y: scroll;
    cursor: pointer;
    border-radius: 4px 5px 5px 4px;
    margin-top: 50px;
    border: 1.5px solid rgb(20, 20, 148);
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background-image: linear-gradient(to bottom, #1824d1 50%, #2f78a8 100%);
    font-family: 'Montserrat', sans-serif;
}
